import React, { useState, useRef, useEffect } from 'react'
import './style.scss'
import { Link } from 'gatsby'
import { useCategorizedPosts } from '@hooks/use-categorized-posts'

interface Props {
  defaultCategory?: string // ê¸°ë³¸ìœ¼ë¡œ ì„ íƒëœ ì¹´í…Œê³ ë¦¬
}

// ìµœëŒ€ 3ë‹¨ê³„ ì¹´í…Œê³ ë¦¬ê¹Œì§€ë§Œ(ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ í¬í•¨) ì§€ì›
const Category: React.FC<Props> = ({ defaultCategory = '' }) => {
  const { totalPosts, categories } = useCategorizedPosts()
  const [selectedCategory, setSelectedCategory] = useState(defaultCategory)

  // ì¹´í…Œê³ ë¦¬ í´ë¦­ ì´ë²¤íŠ¸ ì²˜ë¦¬
  const handleClickCategory = (event, name) => {
    if (event.detail > 1) return // ë”ë¸” í´ë¦­ ì´ìƒì´ë©´ Link ë™ìž‘
    event.preventDefault() // í•œë²ˆ í´ë¦­ì´ë©´ Link ì´ë™ ë§‰ê³ , selecedCategory í† ê¸€
    setSelectedCategory(selectedCategory === name ? '' : name)
  }

  return (
    <nav className="sidebar">
      <Link
        to={`/posts/`}
        className="sidebar__total"
      >{`ì „ì²´ ê¸€ (${totalPosts})`}</Link>
      <ul className="sidebar__category">
        {/* ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ */}
        {categories.map((category) => (
          <li key={category.name}>
            <CategoryName
              name={category.name}
              num={category.num}
              handleClickCategory={handleClickCategory}
            />
            <ul className="sidebar__category">
              {/* ì„œë¸Œ ì¹´í…Œê³ ë¦¬ 1 */}
              {category.subCategories.map((subCategory) => (
                <li key={subCategory.name}>
                  <CategoryName
                    name={subCategory.name}
                    num={subCategory.num}
                    handleClickCategory={handleClickCategory}
                  />
                  <ul className="sidebar__category">
                    {/* ì„œë¸Œ ì¹´í…Œê³ ë¦¬ 2 */}
                    {subCategory.subCategories.map((subCategory) => (
                      <li key={subCategory.name}>
                        <CategoryName
                          name={subCategory.name}
                          num={subCategory.num}
                          handleClickCategory={handleClickCategory}
                        />
                        {/* ì„œë¸Œ ì¹´í…Œê³ ë¦¬ 2 í¬ìŠ¤íŠ¸ */}
                        <Posts
                          posts={subCategory.posts}
                          isSelected={selectedCategory === subCategory.name}
                        />
                      </li>
                    ))}
                  </ul>
                  {/* ì„œë¸Œ ì¹´í…Œê³ ë¦¬ 1 í¬ìŠ¤íŠ¸ */}
                  <Posts
                    posts={subCategory.posts}
                    isSelected={selectedCategory === subCategory.name}
                  />
                </li>
              ))}
            </ul>
            {/* ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ í¬ìŠ¤íŠ¸ */}
            <Posts
              posts={category.posts}
              isSelected={selectedCategory === category.name}
            />
          </li>
        ))}
      </ul>
    </nav>
  )
}

export default Category

// <ì¹´í…Œê³ ë¦¬ ì´ë¦„> ì„œë¸Œ ì»´í¬ë„ŒíŠ¸
const CategoryName: React.FC = React.memo(
  ({ name, num, handleClickCategory }) => {
    return (
      <Link
        to={`/posts/${name}`}
        className="sidebar__category--text"
        onClick={(event) => {
          handleClickCategory(event, name)
        }}
      >
        {name}
        <span className="sidebar__category__num">{`\u00a0\u00a0(${num})`}</span>
      </Link>
    )
  }
)

// <í¬ìŠ¤íŠ¸ ëª©ë¡> ì„œë¸Œ ì»´í¬ë„ŒíŠ¸
const Posts: React.FC = React.memo(({ posts, isSelected }) => {
  const postsRef = useRef<HTMLUListElement>(null)

  // cssë¥¼ ìœ„í•œ ë³€ìˆ˜ ì „ë‹¬
  useEffect(() => {
    const postsEle = postsRef.current
    postsEle &&
      postsEle.style.setProperty('--posts-count', posts.length.toString())
  }, [])

  // ë””ë ‰í† ë¦¬ ì„ íƒ ì—¬ë¶€
  const selected = isSelected ? 'open' : null

  return (
    <ul ref={postsRef} className={`sidebar__posts ${selected}`}>
      {posts.map((post) => (
        <li key={post.id}>
          <Link to={`/posts/${post.relativePath}`} activeClassName="active">
            {post.title || post.name}
          </Link>
        </li>
      ))}
    </ul>
  )
})
