import { graphql, useStaticQuery } from 'gatsby'
import { sortByName } from '@utils/sortByTypes'

// ë§ˆí¬ë‹¤ìš´ íŒŒì¼ ì •ë³´
interface Post {
  id: string
  title: string
  relativePath: string
}

// ì¹´í…Œê³ ë¦¬(ë””ë ‰í† ë¦¬) ì •ë³´
interface Category {
  name: string
  posts: Post[]
  nums: number
  folders: Category[]
}

interface CategorizedPosts {
  totalPosts: number // ì „ì²´ í¬ìŠ¤íŠ¸ ìˆ˜
  categories: Category[] // ì¹´í…Œê³ ë¦¬(ë””ë ‰í† ë¦¬) ì •ë³´ ë°°ì—´
}

export const useCategorizedPosts = (): CategorizedPosts => {
  // ë§ˆí¬ë‹¤ìš´ íŒŒì¼ ìˆ˜ì§‘
  // ì¡°ê±´ 1. isCompleted: true
  // ì¡°ê±´ 2. README.md ì œì™¸
  // ì¡°ê±´ 3. posts ë””ë ‰í† ë¦¬ í•˜ìœ„
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(
        filter: {
          frontmatter: { isCompleted: { eq: true } }
          fileAbsolutePath: { regex: "/^(?!.*README).*posts.*$/" }
        }
        sort: { frontmatter: { title: ASC } }
      ) {
        nodes {
          id
          parent {
            ... on File {
              id
              name
              relativePath
            }
          }
          frontmatter {
            title
          }
        }
      }
    }
  `)

  const totalPosts = data.allMarkdownRemark.nodes.length // ì „ì²´ í¬ìŠ¤íŠ¸ ìˆ˜

  // ë§ˆí¬ë‹¤ìš´ íŒŒì¼ ìˆœíšŒí•˜ë©° ì¹´í…Œê³ ë¦¬ ë¶„ë¥˜
  const categories = []
  data.allMarkdownRemark.nodes.forEach((post) => {
    // ë°ì´í„° ë¶„í•´
    const { id, parent, frontmatter } = post
    const { name, relativePath } = parent
    const { title } = frontmatter

    // ì¹´í…Œê³ ë¦¬ ë¶„ë¥˜ë¥¼ ìœ„í•´ ìƒëŒ€ ê²½ë¡œë¥¼ ë°°ì—´ë¡œ ë³€í™˜
    const pathArray = relativePath.split('/')

    // ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ ë¶„ë¥˜
    const rootCategoryName = pathArray[0]
    let rootCategory = categories.find(
      (category) => category.name === rootCategoryName
    )
    if (!rootCategory) {
      // ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ê°€ ì—†ì„ ê²½ìš°, ìƒˆë¡œ ìƒì„±
      const newCategory = {
        name: rootCategoryName,
        num: 1,
        posts: [],
        subCategories: [],
      }
      categories.push(newCategory)
      rootCategory = newCategory
    } else {
      // ë£¨íŠ¸ ì¹´í…Œê³ ë¦¬ê°€ ìžˆì„ ê²½ìš°, ê°¯ìˆ˜ë§Œ ì—…ë°ì´íŠ¸
      rootCategory.num += 1
    }

    // ì„œë¸Œ ì¹´í…Œê³ ë¦¬ ë¶„ë¥˜
    let parentCategory = rootCategory // ìƒëŒ€ ê²½ë¡œ ë°°ì—´ì„ ìˆœíšŒí•˜ë©°, ë¶€ëª¨ ë””ë ‰í† ë¦¬ ìž¬í• ë‹¹
    pathArray.slice(1, -1).forEach((subCategoryName) => {
      let subCategory = parentCategory.subCategories.find(
        (category) => category.name === subCategoryName
      )
      if (!subCategory) {
        // ì„œë¸Œ ì¹´í…Œê³ ë¦¬ê°€ ì—†ì„ ê²½ìš°, ìƒˆë¡œ ìƒì„±
        const newCategory = {
          name: subCategoryName,
          num: 1,
          posts: [],
          subCategories: [],
        }
        parentCategory.subCategories.push(newCategory)
        parentCategory.subCategories.sort(sortByName) // ì„œë¸Œ ì¹´í…Œê³ ë¦¬ ì´ë¦„ ìˆœ ì •ë ¬
        subCategory = newCategory
      } else {
        // ì„œë¸Œ ì¹´í…Œê³ ë¦¬ê°€ ìžˆì„ ê²½ìš°, ìƒˆë¡œ ìƒì„±
        subCategory.num += 1
      }
      parentCategory = subCategory
    })

    // í¬ìŠ¤íŠ¸ ì‚½ìž…
    parentCategory.posts.push({ id, title, name, relativePath })
  })

  categories.sort(sortByName) // ìµœìƒë‹¨ ì¹´í…Œê³ ë¦¬ ì´ë¦„ ìˆœ ì •ë ¬

  return { totalPosts, categories }
}
